  font-size: 18px;
  font-weight: 500;
  line-height: normal;
  padding: 15px 0;
}
.menu--link:hover {
  color: var(--color-primary);
}
.menu--link:hover svg {
  transform: rotateY(180deg);
}
.menu--item {
  position: relative;
}
.menu--item:last-child {
  margin-right: 0;
}
.menu--item--active > .menu--link {
  color: var(--color-secondary);
}
.menu--item--active > .menu--link--active {
  color: var(--color-secondary);
}
.menu--item--has-child a {
  display: flex;
  gap: 8px;
  align-items: center;
}
.menu--item--has-child svg {
  stroke: #797F89;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
}
.menu--item .second-lavel > .sub--menu {
  left: calc(100% + 1px);
  top: 0;
}
.menu--item .menu--sub {
  position: absolute;
  left: 0;
  top: 100%;
  min-width: 250px;
  max-width: 250px;
  background-color: var(--color-white);
  color: red;
  border-top: none;
  border-radius: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  padding-top: 20px;
  padding-bottom: 20px;
  transform: scaleY(0);
  transform-origin: 0 0 0;
  transition: all 0.4s cubic-bezier(0.3, 0.02, 0, 0.6);
  visibility: hidden;
  opacity: 0;
  z-index: 9;
  display: block;
  padding-left: 10px;
  transition: all 0.4s ease-in-out;
}
.menu--item .menu--sub .menu--item {
  margin: 0;
  position: relative;
}
.menu--item .menu--sub .menu--item::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 0;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  margin: 0;
  background: var(--color-primary);
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
}
.menu--item .menu--sub .menu--item:hover::before {
  opacity: 1;
  visibility: visible;
  height: 11px;
}
.menu--item .menu--sub .menu--link {
  font-size: 0.9375rem;
  padding: 10px 15px 10px 26px;
  display: block;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #180605;
  font-family: var(--font-bhaijaan);
  font-size: 18px;
  font-weight: 500;
  line-height: normal;
}
.menu--item .menu--sub .menu--link:hover {
  color: var(--color-primary);
}
.menu--item:hover > .menu--sub {
  opacity: 1;
  visibility: visible;
  top: 100%;
  transform: scaleY(1);
}

