#preloader {
  position: fixed;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  overflow: visible;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
}
#preloader .loading-wrapper {
  width: 180px;
  height: 180px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  display: flex;
  font-family: Helvetica;
  flex-direction: column;
  justify-content: center;
}
#preloader .loading-text {
  text-align: center;
}
#preloader .circle {
  box-sizing: border-box;
  border-top: 7px solid var(--color-secondary);
  border-bottom: 7px solid transparent;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  border-radius: 100%;
  position: relative;
}
#preloader .circle::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--color-secondary);
  left: 19px;
  top: 17px;
  border-radius: 100px;
}
#preloader .circle::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--color-secondary);
  right: 19px;
  top: 17px;
  border-radius: 100px;
}
#preloader .loading-circle {
  height: 100%;
  width: 100%;
  position: absolute;
  animation: rotate 1.5s infinite;
}
#preloader .loading-circle-small {
  height: 70%;
  width: 70%;
  position: absolute;
  top: 15%;
  left: 15%;
  background: var(--color-secondary);
  display: flex;
  justify-content: center;
  align-items: center;
}
#preloader .loading-circle-small .doc-icon {
  width: 45px;
  height: 60px;
  background: var(--color-white);
  border-radius: 3px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 12px 8px;
}
#preloader .loading-circle-small .doc-icon .line {
  width: 100%;
  height: 4px;
  background: black;
  margin-bottom: 8px;
  border-radius: 15px;
  transition: all 0.4s ease-in-out;
}
#preloader .loading-circle-small .doc-icon .line:nth-child(1) {
  animation: line-fill 0.5s infinite alternate;
